<template>
	<div class="p p-90">
		<el-row style="width: 100%;" :gutter="50">
			<el-col :span="6">
				<el-col :span="12" class="cir" v-for="(item,index) in FaceTotal" :key="index">
					<div style="height:180px;" v-if="item.color">
						<circleHalf :ids="'face'+index" :data='[item]' :colorList="item.color" :total="58"></circleHalf>
					</div>
					<div style="height:180px;" v-else>
						<circleHalf :ids="'face'+index" :data='[item]' :total="58"></circleHalf>
					</div>
					<p class="text"><span>{{item.name|type}}-</span>{{item.name|name}}</p>
				</el-col>
			</el-col>
			<el-col :span="6">
				<div style="max-width: 500px;margin: 0 auto;">
					<img src="../assets/img/title/park-cztj-day.png" style="width: 100%;margin-top: 30px;"/>
					<div class="echartline" style="height: 250px;">
						<myLine ids="cztj" ref="cztj" :data="lineData"></myLine>
					</div>
					<img src="../assets/img/title/park-gqczzb.png" style="width: 100%; margin-top: 60px;" />
					<div class="echartline" style="height: 250px;">
	            		<first echartName="detailMsg" class="my-chart-box" ref="detailMsg" img="/static/img/xf.png" :datas="Basicgeoinfo" v-if="Basicgeoinfo.length>0"></first>                       		
					</div>
				</div>
				
			</el-col>
			<el-col :span="6">
				<div style="max-width: 500px;margin: 0 auto;">
				
					<img src="../assets/img/title/park-xftj-day.png" style="width: 100%;margin-top: 30px;"/>
					<div class="echartline" style="height: 250px;">
						<myLine ids="cztj2" ref="cztj2" :data="lineData"></myLine>
					</div>
					<img src="../assets/img/title/park-hyxf-day.png" style="width: 100%; margin-top: 60px;"/>
					<div class="echartline" style="height: 250px;">
	            		<first echartName="detailMsg2" class="my-chart-box" ref="detailMsg2" img="/static/img/xf2.png" :datas="Basicgeoinfo2" v-if="Basicgeoinfo.length>0"></first>                       		
					</div>
				</div>
			</el-col>
			<el-col :span="6">
				<div style="max-width: 500px;margin: 0 auto;">
					<img src="../assets/img/title/park-xjktj-day.png" style="width: 100%;margin-top: 30px;"/>
					<div class="echartline" style="height: 250px;">
						<myBar ids="pcFb" ref="barFb" :data="barDatas3"></myBar>
					</div>
					<img src="../assets/img/title/park-ykt.png" style="width: 100%; margin-top: 60px;"/>
					<div class="echartline" style="height: 250px;">
						<myBar ids="pcFb3" ref="barFb3" :data="barDatas3"></myBar>
					</div>
				</div>
			</el-col>
		</el-row>
	</div>	
</template>

<script>
import circleHalf from '@/components/module/echarts/circleHalf'
import myLine from '@/components/module/echarts/line.vue'
import first from '@/components/module/echarts/first.vue'
import myBar from '@/components/module/echarts/echartBar'
	export default{
		components:{
			circleHalf,
			myLine,
			first,
			myBar
		},
		filters:{
			type(val){
				return val.split('-')[0]
			},name(val){
				return val.split('-')[1]
			}
		},
		data(){
			return{
				FaceTotal:[{
					'color':['#00ccff','#00ff78'],
				    'name': '当日-充值金额',
				    'value': 2590,
				    'unit':'元'
				},{
				    'name': '当年-充值金额',
				    'value': 2.86,
				    'unit':'元'
				},{
					'color':['#00ccff','#00ff78'],
				    'name': '当日-总消费金额',
				    'value': 896,
				    'unit':'元'
				},{
				    'name': '当年-总消费金额',
				    'value': 2.31,
				    'unit':'亿元',
				   
				},{
					'color':['#00ccff','#00ff78'],
				    'name': '当日-新建卡数',
				    'value': 23,
				    'unit':'张'
				},{
				    'name': '当年-新建卡数',
				    'value': 9379,
				    'unit':'张'
				}],
				lineData:{
					dataAxis:['A区','B区','C区','D区','E区','G区'],
					unit:'(元)',
					data:[8770,5652,7245,6544,5548,5444]
				},
		        Basicgeoinfo: [
		        	{ value: 19,unit:'%',name:'A区' },
		        	{ value: 21,unit:'%',name:'B区' },
		        	{ value: 15,unit:'%',name:'C区' },
		        	{ value: 15,unit:'%',name:'D区' },
		        	{ value: 24,unit:'%',name:'E区' },
		        	{ value: 19,unit:'%',name:'G区'},
		        ],
		         Basicgeoinfo2: [
		        	{ value: 35,unit:'%',name:'餐饮' },
		        	{ value: 11,unit:'%',name:'医疗' },
		        	{ value: 9,unit:'%',name:'交通' },
		        	{ value: 12,unit:'%',name:'教育' },
		        	{ value: 26,unit:'%',name:'IT' },
		        	{ value: 19,unit:'%',name:'其他'},
		        ],
		        barDatas3:{
					title:"",
					data:[72,74,78,86,90,75],
					dataAxis:['A区','B区','C区','D区','E区','G区'],
					unit:''
				},
			}

		}
	}
</script>

<style scoped="scoped">
	.p-90{
		background: url(../assets/img/pay-bg.jpg)no-repeat;
		background-size: 100% 100%;
		padding:200px 90px 150px;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		width: 100%;
		height: 100%;
	}
	.text{
		color: #fff;
		text-align: center;
	}
	.text span{
		 background: linear-gradient(to top, #00ccff, #04d56f);
        -webkit-background-clip: text;
        color: transparent;
	}
	.cir:nth-of-type(2n) .text span{
		background: linear-gradient(to top, #00ccff, #0462d9);
        -webkit-background-clip: text;
        color: transparent;
	}
</style>


